import React, { useEffect,useState } from "react";
import { ParallaxBanner } from "react-scroll-parallax";
import backgroundImg from '../assets/claudio-testa-iqeG5xA96M4-unsplash.jpg'; 
const AdvancedBannerTop = () => {
  const [data, setData] = useState(null);
  const [error, setError] = useState(null);
  useEffect(()=>{
    const fetchData =async()=>{
          try{
                    const responseData=  await fetch("http://localhost:8080/api/documents/66ac8bf9305c023c5163b67e");
                    if(!responseData.ok){
                      throw new Error("Network response was not ok");
                    }
                     const result=await responseData.json();
                     setData(result);

          }
          catch(error){
           console.error("Error fetching data:", error);
            setError("Failed to fetch data. Please try again later.");
          }
    };
    fetchData();
 }, []);
    // const title = "Welcome To Virtual Museum";
    const title =  data?.home?.section1?.heading;
    const titleSplit = title?.split(/(\s+)/);  
    console.log(data, "data");
  const background = {
    // image:"" ,
    translateY: [0, 50],
    opacity: [1, 0.3],
    scale: [1.05, 1, "easeOutCubic"],
    shouldAlwaysCompleteAnimation: true
  };

  const headline = {
    translateY: [0, 30],
    scale: [1, 1.05, "easeOutCubic"],
    shouldAlwaysCompleteAnimation: true,
    expanded: false,
    children: (
      <div className="inset center">
        {/* <h1 className="headline white"></h1> */}
        <h1 className="centered-text fontfamily title-container">
            {titleSplit?.map((char, i) => (
              <span
                key={i}
                className={`animated-title ${char === ' ' ? 'space' : ''}`}
                style={{ animationDelay: `${0.08 * i}s` }}
              >
                {char}
              </span>
            ))}
          </h1>
          <div className="button-container">
            {/* Use standard button if ParallaxButton is not available */}
            <button className="btn-color px-5 py-3 viewbtn"  data-aos="fade-up" data-aos-delay="60 ">Scroll to View </button>
          </div>
      </div>
    )}

  const foreground = {
    image: "backgroundImg",
    translateY: [0, 15],
    scale: [1, 1.1, "easeOutCubic"],
    shouldAlwaysCompleteAnimation: true
  };

  const gradientOverlay = {
    opacity: [0, 1, "easeOutCubic"],
    shouldAlwaysCompleteAnimation: true,
    expanded: false,
    children: <div className="gradient inset" />
  };

  return (
    <div>
    {error && (
      <div className="popup-message">
        <p>{error}</p>
        <button onClick={() => setError(null)}>Close</button>
      </div>
    )}
    <ParallaxBanner
      layers={[background, headline, foreground, gradientOverlay]}
      className="full home_banner"
    />
     </div>
    
  );
};

export default AdvancedBannerTop;

