import React, { useEffect, useState } from "react";
import "../css/sectionCom.css";
import statue1 from "../assets/mona-lisa-2704271_1280.jpg";
import statue2 from "../assets/angle2 (1).png";
import statue3 from "../assets/angle3.jpg";
import statue4 from "../assets/angle4 (2).jpg";
import statue5 from "../assets/statue5.jpg";
import video from "../assets/smokeeffectnew.mp4";

import Navbar from "./Navbar";
import { div } from "three/webgpu";
import Footer from "./Footer";

const SectionCom = () => {
  const[data,setData]=useState({})
  useEffect(()=>{
    const fetchData=async()=>{
      try {
        const response=await fetch("http://localhost:8080/api/documents/66ac8bf9305c023c5163b67e");
        if(!response.ok){
           throw new Error("network  is issuse");
        }
        const result=await response.json();
        setData(result);
      } catch (error) {
       console.error("error:"+error);
      }
    };
    fetchData();
  },[])
 const{about}=data;
  return (
    <div>
    <Navbar/>
    <div className="secionCom">
      <div className="section">
        <div className="video-visual">
          <video
            className="video"
            autoPlay
            loop
            muted
            poster=""
            role="none"
            aria-label="background gradient animation"
          >
            <source
              src={video}
              type="video/mp4"
            />
          </video>
        </div>
        <div className="section-wrapper">
          <div className="content-wrapper">
            <div className="content content-1">
              <div className="mobile-visual">
                <img
                  className="card-img"
                  src={statue3}
                  alt="Fantasy warrior - Video game character"
                />
              </div>
              <div className="meta">
                <h2 className="headline text-white">
                  {/* Ancient Civilizations */}{about?.[0]?.head}
                  <span className="text-bold text-white">{about?.[0]?.subhead}</span>
                </h2>
                <p className="desc text-white">
                  {/* Journey through the sands of time and explore the wonders of
                  ancient Egypt. Discover the secrets of the pyramids, the
                  pharaohs, and the intricate art of hieroglyphics. */}
                  {about?.[0]?.description1}
                </p>
                <p className="desc text-white">
                  {/* Delve into the cradle of civilization and uncover the rich
                  history of Mesopotamia. Learn about the Sumerians,
                  Babylonians, and Assyrians, and their contributions to
                  writing, law, and architecture. */}
                 {about?.[0]?.description2}
                </p>
              </div>
            </div>
            <div className="content content-2">
              <div className="mobile-visual">
                <img
                  className="card-img"
                  src={statue1}
                  alt="Green haired ninja in armor - Video game character"
                />
              </div>
              <div className="meta">
                <h2 className="headline text-white">
                  {/* Renaissance Art */}{about?.[1]?.head}
                  <span className=" text-white text-bold"> {about?.[1]?.subhead}</span>
                </h2>
                <p className="desc text-white">
                  {/* Explore the works of one of the greatest minds of the
                  Renaissance. See Leonardo’s masterpieces, including the Mona
                  Lisa and The Last Supper, and learn about his contributions to
                  art, science, and engineering */}
                   {about?.[1]?.description1}
                </p>
              </div>
            </div>
            <div className="content content-3">
              <div className="mobile-visual">
                <img
                  className="card-img"
                  src={statue2}
                  alt="Female warrior in armor - Video game character"
                />
              </div>
              <div className="meta">
                <h2 className="headline text-white">
                  {/* Ancient Greece  */}{about?.[2]?.head}
                  <span className="text-bold text-white">{about?.[2]?.subhead}</span>
                </h2>
                <p className="desc text-white">
                  {/* In ancient Rome, wealthy individuals would collect art,
                  sculptures, and artifacts in their homes and villas. These
                  private collections were often accessible to guests and served
                  as status symbols. */}{about?.[2]?.description1}
                </p>
              </div>
            </div>
            <div className="content content-4">
              <div className="mobile-visual">
                <img
                  className="card-img"
                  src={statue3}
                  alt="Agile warrior - Video game character"
                />
              </div>
              <div className="meta">
                <h2 className="headline text-white">
                  {/* The Venus de Milo */}{about?.[3]?.head}
                  <span className=" text-white ">{about?.[3]?.subhead}</span>
                </h2>
                <p className="desc text-white">
                  {/* Discovered on the island of Milos, this iconic statue is
                  believed to represent Aphrodite, the Greek goddess of love and
                  beauty.  */}{about?.[3]?.description1}
                </p>
              </div>
            </div>
            <div className="content content-5">
              <div className="mobile-visual">
                <img
                  className="card-img"
                  src={statue5}
                  alt="Agile - Video game "
                />
              </div>
              <div className="meta">
                <h2 className="headline text-white">
                  {/* The Venus de Milo */}{about?.[4]?.head}
                  <span className=" text-white ">{about?.[4]?.subhead}</span>
                </h2>
                <p className="desc text-white">
                  {/* Discovered on the island of Milos, this iconic statue is
                  believed to represent Aphrodite, the Greek goddess of love and
                  beauty.  */}
                  {about?.[4]?.description1}
                </p>
              </div>
            </div>
          </div>
          <div className="visual">
            <div className="card-wrapper">
              <div className="card card-1">
                <img
                  className="card-img"
                  src={statue3}
                  alt="Fantasy warrior - Video game character"
                />
              </div>
              <div className="card card-2">
                <img
                  className="card-img"
                  src={statue2}
                  alt="Green haired ninja in armor - Video game character"
                />
              </div>
              <div className="card card-3">
                <img
                  className="card-img"
                  src={statue3}
                  alt="Female warrior in armor - Video game character"
                />
              </div>
              <div className="card card-4">
                <img
                  className="card-img"
                  src={statue4}
                  alt="Agile warrior - Video game character"
                />
              </div>
              <div className="card card-5">
                <img
                  className="card-img"
                  src={statue5}
                  alt=" warrior - Video game "
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>  
    <Footer/>
    </div>
  );
};

export default SectionCom;
