import React, { useEffect, useState } from "react";
import "../css/contact.css";
import Navbar  from "./Navbar.jsx";
import Footer from '../page/Footer.jsx';
import { useNavigate, useParams } from "react-router-dom";
import emailjs from 'emailjs-com';

const Contact= () => {
  const { sectionI } = useParams();
  useEffect(() => {
    if (sectionI) {
      const element = document.getElementById(sectionI);
      if (element) {
        element.scrollIntoView({ behavior: "smooth" });
      } else {
        // const element = document.getElementById(contact);

        element.scrollIntoView({ behavior: "smooth" });
      }
    }
  }, [sectionI]);
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    name: "",
    phoneNumber: "",
    email: "",
    
  });
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const [errors, setErrors] = useState({});

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
    setErrors({
      ...errors,
      [name]: "",
    });
  };

  const validateForm = () => {
    let formErrors = {};
  

    if (!formData.name) {
        formErrors.name = "Name is required";
    } else if (!/^[a-zA-z\s.]+$/.test(formData.name)) {
        formErrors.name = "Name is invalid";
    }    
    if (!formData.phoneNumber) {
      formErrors.phoneNumber = "Phone number is required";
    } else if (!/^\d{10}$/.test(formData.phoneNumber) || /^0{10}$/.test(formData.phoneNumber)|| (!/^\d+$/.test(formData.phoneNumber)) ) {
      formErrors.phoneNumber = "Phone number is invalid";
    }

   
    if (!formData.email) {
      formErrors.email = "Email is required";
    } else if (!/^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$/.test(formData.email)) {
      formErrors.email = "Email address is invalid";
    }
  
  
    return formErrors;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const formErrors = validateForm();
    if (Object.keys(formErrors).length === 0) {
      console.log("Form data:", formData);
    emailjs.send('service_m1hdy3t', 'template_ml8xk08', formData, 'mvtE9diBY0Ft68S58')
    .then((response) => {
        console.log('Email sent:', response.status, response.text);
    }, (error) => {
        console.error('Error sending email:', error);
    });

      alert("Form submitted successfully!");
      setFormData({
        name: "",
        phoneNumber: "",
        email: "",
       
      });
     navigate("/");
    } else {
      setErrors(formErrors);
    }
  };

  return (
    <>
      <div className=" container-fluid  p-0 overflowX-hidden" id="contact">
          <Navbar/>
      {/* form */}

      <div className="bg-form reach_out_form " >
        <div className="container">
          <div className="py-5 text-center text-white mt-5">
            <h2 className="font-saira fw-bold fontSize">Reach Out To Us</h2>  
          </div>
          {/* form */}
          <div className="row " id="enq">
            <div className="col-lg-6 col-md-12 col-sm-12 col-xs-12 ">
              {/* Form goes here */}
              <div
                className=" bg-black gradient-background"
                data-aos="fade-right"
                data-aos-offset="100"
                data-aos-easing="ease-in-sine"
              >
                <form
                  className="px-3 pt-3 reach_out_frm"
                  onSubmit={handleSubmit}
                >
                  <div className="row ">
                    <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <label className="form-label text-white font-saira-semi-Condensed fw-bold">
                        Name
                      </label>

                      <input
                        className="form-control mb-2 font-roboto"
                        placeholder="Your Full Name"
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                      />
                      {errors.name && (
                        <p className="error-message-font-roboto text-danger">{errors.name}</p>
                      )}
                    </div>
                    <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12 ">
                      <label className="form-label text-white font-saira-semi-Condensed fw-bold">
                        Phone Number
                      </label>
                      {/* <input
                        className="form-control mb-2 font-roboto"
                        placeholder="Your Phone Number"
                        name="phoneNumber"
                        value={formData.phoneNumber}
                        onChange={handleChange}
                      /> */}
                      <input
                        className="form-control mb-2 font-roboto"
                        placeholder="Your phone Number"
                        name="phoneNumber"
                        value={formData.phoneNumber}
                        onChange={handleChange}
                      />
                      {errors.phoneNumber && (
                        <p className="error-message-font-roboto text-danger">{errors.phoneNumber}</p>
                      )}
                    </div>
                    <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <label className="form-label text-white font-saira-semi-Condensed fw-bold">
                        Email Address
                      </label>
                      <input
                        className="form-control mb-2 font-roboto"
                        placeholder="Your Email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                      />
                      {errors.email && (
                        <p className="error-message-font-roboto text-danger">{errors.email}</p>
                      )}
                    </div>
                 
                    <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12 py-5">
                      <div className="d-grid gap-2">
                        <button
                          className="send-btn fw-bold py-2 font-saira-semi-Condensed"
                          type="submit"
                        >
                          Send Messages
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>

            <div className="col-lg-6">
              <div className="row text-start">
                {/* <h1 className="text-white md-pt-3  py-lg-0 py-md-0 py-4 font-saira-semi-Condensed fw-bold  text-lg-start text-md-start text-center">
                  Location
                </h1> */}
                <div className="col-12 map_height ">
                <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d996705.7778072077!2d76.944083784375!3d12.622930081275983!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bae17dae04c4415%3A0xc3d5219f7fdc4844!2sRezwan%20Razack%E2%80%99s%20Museum%20of%20Indian%20Paper%20Money!5e0!3m2!1sen!2sin!4v1722501530032!5m2!1sen!2sin"   
                allowfullscreen
                className="googlemap"></iframe>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
   <Footer/>
      </div>
    </>
  );
}                         
 export default Contact;