import React from "react";
import "../css/Statues.css";
import { Link } from "react-router-dom";
import { div } from "three/webgpu";
import Navbar from "./Navbar";
import Footer from '../page/Footer.jsx';
const Statue = () => {
  return (
    <>
    <div className="statue">
      <Navbar/>
      <div className="parallax-wrapper">
        <div className="background background--spring ">
          {/* <h2 className="title display-inline">Spring</h2> */}

        </div>
        <section className="season-desc">
          <div className="desc-wrapper">
            <h3 className="season--title text-white text-uppercase">
              Holocaust Memorial
            </h3>
            <p className="season--about container text-white">
              Contemporary museum development has been much influenced by
              changing policies in public sector finance. In many countries the
              contribution of public funds to museums has remained static or has
              fallen, so that museums’ governing bodies and directors have had
              to seek funding from alternative sources. 
             
            </p>
          </div>
        </section>
        <div className="background background--summer">
          {/* <h2 className="title">Summer</h2> */}
        </div>
        <section className="season-desc">
          <div className="desc-wrapper">
            <h3 className="season--title  text-uppercase">
              Evidence from antiquity
            </h3>
            <p className="season--about container  text-white">
              The origins of the twin concepts of preservation and
              interpretation, which form the basis of the museum, lie in the
              human propensity to acquire and inquire. Collections of objects
              have been found in Paleolithic burials, while evidence of inquiry
              into the environment, and communication of the findings, can be
              seen in the cave and mobiliary art of the same period. 
            </p>
          </div>
        </section>
        <div className="background background--fall">
          {/* <h2 className="title">Autumn</h2> */}
        </div>
        <section className="season-desc">
          <div className="desc-wrapper">
            <h3 className="season--title BokorFamily text-uppercase">
              Classical collecting
            </h3>
            <p className="season--about container Alegreyafont">
              The archaeological and historical records do not provide evidence
              that the museum as it is known today developed in such early
              times, nor does the word museum support this, despite its
              classical origin. Nevertheless, the collection of things that
              might have religious, magical, economic, aesthetic, or historical
              value or that simply might be curiosities was undertaken worldwide
              by groups as well as by individuals. In the Greek and Roman
              empires the votive offerings housed in temples, sometimes in
              specially built treasuries, are but one example: they included
              works of art and natural curiosities, as well as exotic items
              brought from far-flung parts of the empires, and they were
              normally open to the public, often upon payment of a small fee.
              Closer to the concept of a museum was the Greek pinakotheke, such
              as that established in the 5th century BCE on the Acropolis at
              Athens, which housed paintings honouring the gods. Nor was there a
              lack of public interest in art at Rome. Indeed, art abounded in
              the public places of Rome, but there was no museum. The
              inaccessibility of the collection of more than one Roman emperor
              was the subject of public comment, and Agrippa, a deputy of
              Augustus, commented in the 1st century BCE to the effect that
              paintings and statues should be available to the people.
            </p>
          </div>
        </section>
        <div className="background background--winter">
          {/* <h2 className="title">Winter</h2> */}
        </div>
        <section className="season-desc">
          <div className="desc-wrapper">
            
            <h3 className="season--title BokorFamily text-uppercase ">
              Journal of Museology
            </h3>
            <p className="season--about container Alegreyafont">
              Architects of many of these modern museums sought to imbue their
              new spaces with symbolism and significance. At the United States
              Holocaust Memorial Museum, American architect James Ingo Freed
              created a space intended to render the visitor slightly
              off-balance and on edge. The Holocaust Memorial Centre in
              Budapest, with a new wing designed by István Mányi, resides in an
              asymmetrical building with dislocated walls, which are intended to
              symbolize the “distorted and twisted” era of the Holocaust.
              Architect Stanley Tigerman’s design for the Illinois Holocaust
              Museum and Education Center divided the building into “dark” and
              “light” sections. Visitors “descend into the darkness” to view
              exhibits revealing the horrors of the time period. They then view
              a Book of Remembrance with the names of the victims at the centre
              of the museum before exiting through a portion of the building
              filled with natural sunlight that symbolizes the time of rescue
              and renewal. Beginning in the 1990s, Holocaust museums, as well as
              organizations independent of them, have organized vast digital
              collections to archive information about the victims and
              survivors. Perhaps the largest and best-known of these collections
              is sponsored by Yad Vashem; visitors to its Web site can view a
              database of victims’ names and outcomes, as well as an online
              photo archive. The Digital Monument to the Jewish Community in the
              Netherlands has an interactive map illustrating all known records
              of Dutch Jews involved in the Holocaust. In the United States the
              Fortunoff Video Archive, on the campus of Yale University, owns
              more than 4,300 videotaped interviews with Holocaust witnesses and
              survivors, and the Shoah Foundation Institute for Visual History
              at the University of Southern California has archives containing
              tens of thousands of interviews.
            </p>
         
          </div>
        
        </section>
         <Footer/>
      </div>
         
    </div>
    </>
  );
};

export default Statue;
