import React, { useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import 'swiper/css/mousewheel'; // Import Mousewheel CSS if required
import '../css/Silder.css';
import { EffectCoverflow, Pagination, Navigation, Mousewheel } from 'swiper/modules';
import photo1 from '../assets/ai-generated-8396175_1280.png';
import photo2 from '../assets/sculpture-1225487_1280.jpg';
import photo3 from '../assets/kitchen-1566967_1280.jpg';
import photo4 from '../assets/landscape-4688945_1280.jpg';
import photo5 from '../assets/diamond-1199183_1280.jpg';
import photo6 from '../assets/crown-6728331_1280.jpg';

import { Link } from 'react-router-dom';

const Slider = () => {
  const[data,setData]= useState({});

  // Calculate the index of the middle slide
  const totalSlides = 6;
  const middleSlideIndex = Math.floor(totalSlides / 2);

  // Function to handle scroll disabling
  const handleScroll = (disable) => {
    if (disable) {
      document.body.classList.add('no-scroll');
    } else {
      document.body.classList.remove('no-scroll');
    }
  };

  useEffect(() => {
    handleScroll(true); // Disable scroll when component mounts

    return () => {
      handleScroll(false); // Enable scroll when component unmounts
    };
  }, []);
    
useEffect(()=>{
  const fetchData =async()=>{
        try{
                  const responseData=  await fetch("http://localhost:8080/api/documents/66ac8bf9305c023c5163b67e");
                  if(!responseData.ok){
                    throw new Error("Network response was not ok");
                  }
                   const result=await responseData.json();
                   setData(result);
        }
        catch(error){
         console.error("Error fetching data:", error);

        }
  };
  fetchData();
}, []);

const { home, about } = data;

  return (
    <>
   
    <div className="slider-component ">
      <div className="containers bg-white">
        <h1 className='text-center text-black text-uppercase text-bold'>
          {/* Early Modern Period */}{home?.section3?.subtitle}
          </h1>
     
        <Swiper
          effect={'coverflow'}
          grabCursor={true}
          centeredSlides={true}
          loop={false}
          loopAdditionalSlides={1}  // Ensures smooth looping
          slidesPerView={'auto'}
          coverflowEffect={{
            rotate: 0,
            stretch: 0,
            depth: 100,
            modifier: 2.5,
          }}
          pagination={{ el: '.swiper-pagination', clickable: true }}
          navigation={{
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
            clickable: true,
          }}
          mousewheel={{ invert: false }} // Set mousewheel configuration
          modules={[EffectCoverflow, Pagination, Navigation, Mousewheel]}
          className="swiper_container container"
          initialSlide={middleSlideIndex} // Set the initial slide
        >
          <SwiperSlide>
            <img src={photo1} alt="slider1" className='styleImg' />
          </SwiperSlide>
          <SwiperSlide>
            <img src={photo2} alt="slider2" className='styleImg' />
          </SwiperSlide>
          <SwiperSlide>
            <img src={photo3} alt="slider3" className='styleImg' />
          </SwiperSlide>
          <SwiperSlide>
            <img src={photo4} alt="slider4" className='styleImg'/>
          </SwiperSlide>
          <SwiperSlide>
            <img src={photo5} alt="slider5" className='styleImg'/>
          </SwiperSlide>
          <SwiperSlide>
            <img src={photo6} alt="slider6" className='styleImg'/>
          </SwiperSlide>
          <SwiperSlide>
            <img src={photo3} alt="slider7" className='styleImg'/>
          </SwiperSlide>
          <div className="slider-controler">
            <div className="swiper-button-prev slider-arrow">
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-left-circle svg-icon" viewBox="0 0 16 16">
                <path fillRule="evenodd" d="M1 8a7 7 0 1 0 14 0A7 7 0 0 0 1 8m15 0A8 8 0 1 1 0 8a8 8 0 0 1 16 0m-4.5-.5a.5.5 0 0 1 0 1H5.707l2.147 2.146a.5.5 0 0 1-.708.708l-3-3a.5.5 0 0 1 0-.708l3-3a.5.5 0 1 1 .708.708L5.707 7.5z"/>
              </svg>
            </div>
            <div className="swiper-button-next slider-arrow">
              <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-arrow-right-circle svg-icon" viewBox="0 0 16 16">
                <path fillRule="evenodd" d="M1 8a7 7 0 1 0 14 0A7 7 0 0 0 1 8m15 0A8 8 0 1 1 0 8a8 8 0 0 1 16 0M4.5 7.5a.5.5 0 0 0 0 1h5.793l-2.147 2.146a.5.5 0 0 0 .708.708l3-3a.5.5 0 0 0 0-.708l-3-3a.5.5 0 1 0-.708.708L10.293 7.5z"/>
              </svg>
            </div>
            <div className="swiper-pagination"></div>
          </div>
        
          </Swiper>
      </div>
    </div>
    
    </>
  );
};

export default Slider;
