import React, { useEffect, useState } from 'react';
import FlipCard from './FlipCard';
import card from '../assets/diamond-1199183_1280.jpg';
import image1y from '../assets/pexels-andrew-2123337.jpg';
import image2y from '../assets/pexels-pixabay-277054.jpg';
import image3y from '../assets/pexels-unalaurencic-20967.jpg';
import image4y from '../assets/sculpture-1225487_1280.jpg';
import image5y from '../assets/pexels-pixabay-277054.jpg';
import '../css/FlipCardContainer.css'; // Import CSS for the FlipCardContainer component

const FlipCardComponent = () => {
  const[data,setData]= useState({});
   useEffect(()=>{
     const fetchData=async()=>{
      try {
        const response=await fetch("http://localhost:8080/api/documents/66ac8bf9305c023c5163b67e")
        if(!response.ok){
          throw new Error("network issuse");
        }
       const result = await response.json();
       setData(result);

      } catch (error) {
        
      }
    };
    fetchData();
   },[])
   const { home, about } = data;
  useEffect(() => {
    const preventWindowScroll = (event) => {
      const container = document.querySelector('.centerflipcards');
      if (container && container.contains(event.target)) {
        event.preventDefault();
      }
    };

    window.addEventListener('scroll', preventWindowScroll, { passive: false });

    return () => {
      window.removeEventListener('scroll', preventWindowScroll);
    };
  }, []);

  const frontImages = [
    'https://images.unsplash.com/photo-1477313372947-d68a7d410e9f?dpr=1&auto=format&crop=entropy&fit=crop&w=1500&h=1000&q=80&cs=tinysrgb',
     image1y,
     image2y
  ];

  const backImages = [
    image3y,
    image4y,
    'https://images.unsplash.com/photo-1477313372947-d68a7d410e9f?dpr=1&auto=format&crop=entropy&fit=crop&w=1500&h=1000&q=80&cs=tinysrgb'
  ];

  const frontContents = [
    {
      image: card,
      title: home?.section3?.card[0]?.title,
      subtitle:  home?.section3?.card[0]?.subtitle,
    },
    {
      image: card,
      title:  home?.section3?.card[1]?.title,
      subtitle:  home?.section3?.card[1]?.subtitle,
    },
    {
      image: card,
      title:home?.section3?.card[2]?.title,
      subtitle: home?.section3?.card[2]?.subtitle,
    }
  ];

  const backContents = [
    { title:  home?.section3?.card[0]?.name},
    { title:  home?.section3?.card[1]?.name },
    { title: home?.section3?.card[2]?.name}
  ];


  return (
    <div className="centerflipcards">
      {frontImages.map((_, index) => (
        <FlipCard
          key={index}
          frontImage={frontImages[index]}
          backImage={backImages[index]}
          frontContent={frontContents[index]}
          backContent={backContents[index]}
        />
      ))}
    </div>
  );
};

export default FlipCardComponent;
