// FlipCard.js
import React from 'react';
import '../css/FlipCard.css'; // Import CSS for the FlipCard component

const FlipCard = ({ frontImage, backImage, frontContent, backContent }) => {
  return (
    <div className="square-flip">
      <div className="square" style={{ backgroundImage: `url(${frontImage})` }}>
        <div className="square-container">
          <h2>{frontContent.title}</h2>
          <h3>{frontContent.subtitle}</h3>
        </div>
        <div className="flip-overlay"></div>
      </div>
      <div className="square2" style={{ backgroundImage: `url(${backImage})` }}>
        <div className="square-container2">
          <h2>{backContent.title}</h2>
        </div>
        <div className="flip-overlay"></div>
      </div>
    </div>
  );
};

export default FlipCard;
