import React, { useEffect, useRef,useState } from "react";
import { ParallaxProvider } from "react-scroll-parallax";
import { Link } from "react-router-dom";
import AdvancedBannerTop from "./AdvancedBannerTop";
import img2 from "../assets/DeWatermark.ai_1721825535371.png";
import img3 from "../assets/DeWatermark.ai_1721887010024.png";
import img4 from "../assets/paris-france-march-202013-louvre-museum-at-dusk-on-march-20-2013-in-GRMY3K_(1)-transformed.jpeg";
import FlipCardComponent from "./FlipCardCompontent.jsx";
import gsap from "gsap";
import Navbar from "./Navbar";
import Footer from "../page/Footer.jsx";
import Slider from "../page/Slider.jsx";

export default function About() {
  const [data, setData] = useState({});
  const photobannerRef = useRef(null);
  const[error,setError]=useState(false);

  const handleWheel = (event) => {
    if (photobannerRef.current) {
      photobannerRef.current.scrollLeft += event.deltaY;
      event.preventDefault(); // Prevent default scroll behavior
    }
  };

  const preventWindowScroll = (event) => {
    if (photobannerRef.current && photobannerRef.current.contains(event.target)) {
      event.preventDefault();
    }
  };

  useEffect(() => {
    window.addEventListener("wheel", preventWindowScroll, { passive: false });

    return () => {
      window.removeEventListener("wheel", preventWindowScroll);
    };
  }, []);
  
useEffect(()=>{
   const fetchData =async()=>{
         try{
                   const responseData=  await fetch("http://localhost:8080/api/documents/66ac8bf9305c023c5163b67e");
                   if(!responseData.ok){
                     throw new Error("Network response was not ok");
                   }
                    const result=await responseData.json();
                    setData(result);
         }
         catch(error){
          console.error("Error fetching data:", error);
          setError(true);
         }
   };
   fetchData();
}, []);

  useEffect(() => {
    gsap.to(".hero", {
      display: "block",
      position: "sticky",
      top: 0,
      scrollTrigger: {
        trigger: ".hero",
        start: "top top",
        end: "+=800",
        scrub: true,
      },
    });

    gsap.fromTo(".fade-up", { y: "150px", opacity: 0.5 }, {
      opacity: 1, y: 0, ease: "back", duration: 1, stagger: 0.5
    });
    gsap.to(".sec-2", {
      display: "block",
      position: "sticky",
      top: "-30%",
      scrollTrigger: {
        trigger: ".sec-2",
        start: "top -50%",
        end: "+=300px",
        scrub: true,
      },
    });
  }, []);

  const { home, about } = data;
  return (
    <div>
      <ParallaxProvider>
        <Navbar />
        <div className="hero">
          <AdvancedBannerTop />
        </div>
    
        <div className="bg-color bg-black sec-2 pt-5 section-gap">
          <div className="full container p-5">
            <div className="row">
              <div className="col-7">
                <p id="louvre-museum"  className="text-color content fontfamily">
                  {/* The Louvre Museum */}{home?.section2[0]?.title}
                </p>
                <p className="sub-text-color justify-for-content font-size-18 fade-up fontfamily">
                  {/* The Louvre Museum, known as Musée du Louvre in French, is the
                  world's largest and most visited art museum. Located in the
                  heart of Paris, it is housed in a historic palace, originally
                  built as a fortress in the late 12th century under Philip II. */}
                  {home?.section2[0]?.description}
                </p>
                <p className="text-color content fontfamily">
                  {/* Early History */}{home?.section2[1]?.title}
                </p>
                <p className="sub-text-color justify-for-content font-size-18 fontfamily">
                  {/* The Louvre began as a fortress built by King Philip II in the
                  late 12th century to protect Paris from potential invasions.
                  The fortress's remnants can still be seen in the museum’s
                  basement. */}  {home?.section2[1]?.description}
                </p>
              </div>
              <div className="col-5">
                <div className="row ps-5">
                  <div className="col-6 ">
                    <img
                      src={img2}
                      alt="First Image"
                      className="img-fluid img-1 img-border"
                      data-aos="zoom-in"
                      data-aos-delay="100"
                    />
                  </div>
                  <div className="col-6">
                    <img
                      src={img3}
                      alt="Second Image"
                      className="img-fluid img-2 img-border"
                      data-aos="zoom-in"
                      data-aos-delay="100"
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="row mt-5 mb-5">
              <div className="col-6">
                <div className="col-12 text-center pt-3">
                  <img
                    src={img4}
                    alt="Third Image"
                    className="img-fluid img-border"
                    data-aos="zoom-in"
                    data-aos-delay="100"
                  />
                </div>
              </div>
              <div className="col-6">
                <p className="text-color content fontfamily" data-aos="fade-right">
                  {/* Middle Ages */}{home?.section2[2]?.title}
                </p>
                <p
                  className="sub-text-color justify-for-content font-size-18 fontfamily"
                  data-aos="fade-right"
                >
                  {/* During the medieval period, collections were primarily held by
                  the Church and universities. Monasteries safeguarded religious
                  relics, manuscripts, and liturgical objects. Universities like
                  those in Bologna and Oxford had collections used for
                  educational purposes, preserving classical texts and scholarly
                  works. */}{home?.section2[2]?.description}
                </p>
                <p className="text-color content fontfamily " data-aos="fade-right">
                  {/* Early History */}{home?.section2[3]?.title}
                </p>
                <p
                  className="sub-text-color justify-for-content font-size-18 fontfamily"
                  data-aos="fade-right"
                >
                  {/* The Louvre began as a fortress built by King Philip II in the
                  late 12th century to protect Paris from potential invasions.
                  The fortress's remnants can still be seen in the museum’s
                  basement. */}{home?.section2[3]?.description}
                </p>
              </div>
            </div>
          </div>
        </div>
        <div id="history-of-embalming"  className="sec-3 bg-white position-relative history" >
          <p
            className="p-5 text-center head-text fontfamily"
            data-aos="zoom-in"
          >
            {/* History of Embalming */}{home?.section3?.title}
          </p>
          <FlipCardComponent />
          <div className="row">
            <div className="button-container p-5">
              <Link
                to="/explore"
                className="btn-colors px-5 py-3 viewbtn visite-btn"
                data-aos="fade-up"
                data-aos-delay="60"
              >
                VISITE
              </Link>
            </div>
          </div>
          <Slider />
          <Footer />
        </div>
      </ParallaxProvider>
      {error && (
        <div className="popup">
          <p>Backend la irundhu data varala</p>
          <button onClick={() => setError(false)}>Close</button>
        </div>
      )}
    </div>
  );
}
